import Link from 'gatsby-link'
import React from 'react'
import PropTypes from 'prop-types'
import { rhythm } from '../utils/typography'
import { wordWrap } from 'polished'
import styles from '../styles'
import styled from 'styled-components'
import Img from 'gatsby-image'

const ListWrapper = styled.ul`
  width: 100%;
  margin: ${rhythm(1)} auto;
`

const PostLink = styled(Link)`
  display: block;
  background-color: #fff;
  border-radius: 2px;
  border: 1px solid rgba(0, 0, 0, 0.09);
  margin: ${rhythm(1.75)} 0;

  &:first-child {
    margin-top: 0;
  }
  &:last-child {
    margin-bottom: 0;
  }
`

const PostImageWrap = styled.div`
  border-bottom: 1px solid rgba(0, 0, 0, 0.09);

  & .gatsby-image-outer-wrapper {
    position: relative;
    height: ${rhythm(6)};
    overflow: hidden;

    ${styles.media.Phablet} {
      height: ${rhythm(10)};
    }
  }

  & .gatsby-image-wrapper {
    position: absolute;
    overflow: hidden;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    transform: translate(-50%, -50%);
  }
`

const PostInfo = styled.div`
  padding: ${rhythm(3 / 4)} ${rhythm(1)};
`

const PostTitle = styled.h2`
  ${wordWrap('keep-all')};
  line-height: ${rhythm(1.4)};
  font-weight: 700;
  margin: 0;
  font-size: 1.4rem;
`
const PostSubTitle = styled.h2`
  line-height: ${rhythm(1)};
  font-weight: 400;
  margin: 0;
  font-size: 1rem;
  color: rgba(0, 0, 0, 0.54);
`

const PostCreatedAt = styled.time`
  display: block;
  font-size: 0.85rem;
  font-weight: 400;
  color: rgba(0, 0, 0, 0.44);
  margin-top: ${rhythm(1 / 2)};
`

/**
 *  
 *
 * @class PostList
 * @extends {Component}
 */
class PostList extends React.Component {
  static propTypes = {
    posts: PropTypes.array,
  }

  render() {
    const posts = this.props.posts

    return (
      <ListWrapper>
        {posts.map(post => (
          <PostLink key={post.node.fields.slug} to={post.node.fields.slug}>
            {post.node.frontmatter.mainImage && (
              <PostImageWrap>
                <Img sizes={post.node.frontmatter.mainImage.childImageSharp.sizes} />
              </PostImageWrap>
            )}

            <PostInfo>
              <PostTitle>{post.node.frontmatter.title}</PostTitle>
              {post.node.frontmatter.subTitle && (
                <PostSubTitle>{post.node.frontmatter.subTitle}</PostSubTitle>
              )}
              <PostCreatedAt>{post.node.frontmatter.date}</PostCreatedAt>
            </PostInfo>
          </PostLink>
        ))}
      </ListWrapper>
    )
  }
}

export default PostList



